  line-height: 1.5;
}

.hero-trend__btn {
  color: #000;
  cursor: pointer;
  background-color: #f4900d;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 1.2rem;
  transition: all .3s;
  display: inline-block;
}

